.site-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 0 0 0 10px;

  @media (max-width: $tablet-width) {
    flex-direction: column;
    justify-content: flex-start;
    margin-bottom: 41px;
    padding: 0;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 30px;
  }
}

.site-list li:not(:last-of-type) {
  margin-right: 35px;

  @media (max-width: $tablet-width) {
    margin: 0 0 21px;
  }
}

.site-list__link {
  font-size: 16px;
  line-height: 24px;
  color: $black-wood;
  text-decoration: none;

  @media (max-width: $tablet-width) {
    font-size: 24px;
    line-height: 36px;
    color: $white;
  }

  &:hover,
  &:focus {
    outline: none;
    opacity: 0.7;
  }

  &:active {
    opacity: 0.5;
  }
}
